<template>
  <div class="groupdetail">
    <div class="navHead">
      <a-icon type="left" class="leftIcon" @click="backRouter" />
      <span>聊天信息</span>
    </div>

    <div class="scoll_wrapper" ref="wrapper">
      <ul>
        <li>
          <span>群ID：{{$route.params.groupid}}</span>
        </li>
        <li>
          <img src="../assets/image/timg.jpg" alt />
          <div class="content">
            <h3>{{group[group.length - 1].owner}}</h3>
          </div>
        </li>
        <li v-for="(item, index) in members" :key="index">
          <img src="../assets/image/timg.jpg" alt />
          <div class="content">
            <h3>{{item}}</h3>
          </div>
        </li>
      </ul>
    </div>

    <div class="btn">
      <a-button type="primary" @click="showModal" class="addBtn">添加好友</a-button>
      <a-modal
        title="添加好友"
        v-model="visible"
        @ok="handleOk"
        class="a-modal"
        okText="添加"
        cancelText="取消"
        :zIndex="zIndex"
        :closable="closable"
      >
        <a-input placeholder="请输入添加好友的用户名" v-model="username" ref="userNameInput">
          <a-icon slot="prefix" type="user" />
          <a-icon slot="suffix" type="close-circle" @click="emitEmpty" />
        </a-input>
      </a-modal>
      <a-button type="primary" class="quitBtn">退出群组</a-button>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { mapGetters, mapActions, mapState } from "vuex";
// import { message } from "ant-design-vue";
import BScroll from "better-scroll";
export default {
  name: "groupdetail",
  data() {
    return {
      msg: "",
      zIndex: 9999,
      closable: false,
      visible: false,
      username: ""
      //   members: [],
      //   owner: ''
    };
  },
  computed: {
    ...mapState(["group"]),
    members() {
      let members = [];
      this.group.forEach(item => {
        if (item.member) {
          members.push(item.member);
        }
      });
      return members;
    }
  },
  methods: {
    ...mapActions(["inviteToGroup", "listGroupMember"]),
    backRouter() {
      this.$router.go(-1);
      //   this.$router.push({name:'message'})
    },
    showModal() {
      this.visible = true;
    },
    emitEmpty() {
      this.$refs.userNameInput.focus();
      this.username = "";
    },
    handleOk() {
      if (this.username.trim()) {
        let obj = { user: this.username, gid: this.$route.params.groupid };
        this.inviteToGroup(obj);
        this.listGroupMember(this.$route.params.groupid);
        this.visible = false;
        this.initBetterScroll();
        // message.success("已将好友拉进群组！");
      }
      this.username = "";
    },
    initBetterScroll() {
      if (this.wrapperScroll) {
        // 如果存在代表menuScroll已经初始化过一次了
        this.wrapperScroll.refresh(); // 重新计算容器与内容的高度,无需重新实例化新对象
      } else {
        this.wrapperScroll = new BScroll(this.$refs.wrapper, {
          click: true
        }); // 该页面被注销好去清除他
      }
    }
  },
  mounted() {
    this.initBetterScroll();
  },
  beforeDestroy() {
    this.wrapperScroll.destroy();
    this.wrapperScroll = null;
  }
};
</script>

